.price {
	direction: rtl;
	width: 230px;
	font-size: 20px;
	border: 1px solid rgb(209, 209, 209);
	outline: none;
	border-radius: 3px;
	padding: 2px 10px;
	transition: all .3s;
	&:focus {
		outline: none;
	}
	&:hover {
		border: 1px solid rgb(93, 150, 255);
		box-shadow: 0 0 1px rgb(105, 157, 255);
	}
	@media (max-width: 1024px) {
		width: 100%;
	}
}

.inputinvalid {
	border: 1px solid red !important;
}
